import React from 'react'

const Mailchimp = () => {
    return (
        <div id="mc_embed_signup">
            <form action="https://gmail.us10.list-manage.com/subscribe/post?u=45b87401937e89bbe06f75105&amp;id=dadddd1ea3" method="post" id="mc-embedded-subscribe-form" name="mc-embedded-subscribe-form" class="validate" target="_self" novalidate>
                <div id="mc_embed_signup_scroll">
                        
                     <div class="mc-field-group">
                        <label for="mce-EMAIL">Email Address </label>
                        <input type="email"  name="EMAIL" class="required email" id="mce-EMAIL"/>
                    </div>
                            
                    <div id="mce-responses" class="clear">
                        <div class="response" id="mce-error-response" style={{display:"none"}}></div>
                        <div class="response" id="mce-success-response" style={{display:"none"}}></div>
                    </div>   
                    
                    <div style={{position:"absolute", left:"-5000px"}} aria-hidden="true"><input type="text" name="b_45b87401937e89bbe06f75105_dadddd1ea3" tabindex="-1" value=""/></div>
                    <div class="clear"><input type="submit" value="Subscribe" name="subscribe" id="mc-embedded-subscribe" class="button"/></div>
                 </div>
            </form>
        </div>
        
    )
}

export default Mailchimp
